
import { reactive, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useI18n } from "vue-i18n";
import SideBarMenu from "../components/sidebar-menu.vue";
import TabsBar from "../components/tabs.vue";
import MyHeader from "../components/header.vue";
import { MenuItem } from "../components/components";
import About from "./About.vue";
import PluginView from '../components/plugin-view.vue'

export default {
  components: {
    SideBarMenu,
    MyHeader,
    TabsBar,
    PluginView
  },
  setup() {
    const router = useRouter()
    const { t } = useI18n();
    const data: MenuItem[] = [
      {
        name: t("side.menu.webshell_connections"),
        index: "about",
        icon: "el-icon-edit"
      },
      {
        name: "test123",
        index: "test123",
        icon: "el-icon-edit"
      },
      {
        name: "sdffasdfadsf",
        index: "testtest1",
        icon: "el-icon-edit"
      }
    ];
    const menus = reactive(data);
    onMounted(() => {
      router.addRoute("Home", {
        path: "/about",
        name: "About",
        component: About,
        meta:{
          title: t("side.menu.webshell_connections")
        }
      });
      router.addRoute("Home", {
        path: "/test123",
        name: "Test123",
        component: About,
        meta:{
          title: "sdfsf"
        }
      });
      router.addRoute("Home", {
        path: "/testtest1",
        name: "sdfdfsdf",
        component: About,
        meta:{
          title: "水电费第三方"
        }
      });
    });

    return { menus, t };
  }
};
